import React, { useMemo } from "react";
import { useLocation } from "@reach/router";
import CandidateDetailFrame from "../../../componets/cms/mini-studio/CandidateDetailFrame";

/** ‚úÖ SSR-safe pathname */
function getPathnameSafe(loc: ReturnType<typeof useLocation> | any): string {
  if (loc && typeof loc.pathname === "string") return loc.pathname;
  if (typeof window !== "undefined" && window.location) return window.location.pathname || "";
  return "";
}

/** ‚úÖ SSR-safe search string */
function getSearchSafe(loc: ReturnType<typeof useLocation> | any): string {
  if (loc && typeof loc.search === "string") return loc.search;
  if (typeof window !== "undefined" && window.location) return window.location.search || "";
  return "";
}

/** ‚úÖ Safely decode possible base64 fields */
function safeDecodeValue(value: string): string {
  try {
    // detect our custom base64 pattern b64(...)
    if (/^b64\([A-Za-z0-9+/=]+\)$/.test(value)) {
      return atob(value.slice(4, -1));
    }
    // if looks like raw base64 (for compatibility)
    if (/^[A-Za-z0-9+/=]+$/.test(value) && value.length % 4 === 0) {
      const decoded = atob(value);
      // only return if it looks like a URL (defensive)
      if (/^https?:\/\//i.test(decoded)) return decoded;
    }
  } catch (_) {
    // ignore decode errors
  }
  return value;
}

const CandidatePage: React.FC = () => {
  const location: any = useLocation();
  const candidateFromState = location?.state?.candidate || null;

  const pathname = getPathnameSafe(location);
  const search = getSearchSafe(location);

  /** Extract :id from URL path */
  const id = useMemo(() => {
    const parts = (pathname || "").replace(/\/+$/, "").split("/");
    return parts[parts.length - 1] || undefined;
  }, [pathname]);

  /** üîé Dynamically decode all query params */
  const candidateFromQuery = useMemo(() => {
    if (!search) return null;
    const params = new URLSearchParams(search);
    const obj: Record<string, string> = {};

    for (const [key, val] of params.entries()) {
      obj[key] = safeDecodeValue(val);
    }

    // if it‚Äôs basically empty, skip
    const hasUseful = Object.keys(obj).length > 0;
    if (!hasUseful) return null;

    // ensure id from path always included
    if (id && !obj.id) obj.id = id;

    return obj;
  }, [search, id]);

  /** Priority: state object (fast) ‚Üí query (rewrite) ‚Üí null */
  const candidate = candidateFromState || candidateFromQuery || null;

  console.groupCollapsed("üß† Candidate Decode Debug");
  console.log("State candidate:", candidateFromState);
  console.log("Query candidate:", candidateFromQuery);
  console.log("Final candidate:", candidate);
  console.groupEnd();
  
  return <CandidateDetailFrame candidate={candidate} />;
};

export default CandidatePage;
